:root {
  --size: 6px;
}

.container {
  width: 80vw;
  margin: auto;
  display: flex;
  flex-direction: column;
  font-size: calc(var(--size) + 2vmin);
}

.chat-box {
  width: 100%;
  height: 70vh;
  display: inherit;
  overflow-y: auto;
  flex-direction: column-reverse;
  position: relative;
  border: 3px solid black;
}

.form {
  display: flex;
  flex-direction: row;
}
.form > * {
  flex: 2 1 auto;
}

.input, .button {
  font-size: calc(var(--size) + 2vmin);
}

#messages > div {
  padding: 5px;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}